import { Component , ViewChild } from '@angular/core';
import {  ModalController, AlertController,ViewController,IonicPage,  NavController,Events , ActionSheetController, NavParams } from 'ionic-angular';
import { FormControl,  FormBuilder,  FormGroup,   Validators } from '@angular/forms';
import { Auth,NotificationProvider } from '../../../providers';
import { TranslateService } from '@ngx-translate/core';
import { Storage } from '@ionic/storage';
import { ImageProvider } from '../../../providers/image/image';
import { Camera } from '@ionic-native/camera';
import { IonicSelectableComponent } from 'ionic-selectable';
import { Form } from '../../../types';




@IonicPage()
@Component({
  selector: 'page-reglamento-create',
  templateUrl: 'reglamento-create.html',
})
export class ReglamentoCreatePage {
  @ViewChild('fileInput') fileInput;
  @ViewChild('fileInput') fileInput2;
  @ViewChild('portComponent') portComponent: IonicSelectableComponent;
  @ViewChild('portComponent2') portComponent2: IonicSelectableComponent;
  @ViewChild('portComponent3') portComponent3: IonicSelectableComponent;
  @ViewChild('portComponent4') portComponent4: IonicSelectableComponent;
 // @ViewChild('editorx') Editorx;
  public form                  : FormGroup;
  username: string = "";
  public nidevento                 : any;
  public cidevento                 : any;
  public idevento                 : any;
  public nombre                 : any;
  public fecha                 : any;
  public titulo                 : any;
idafiliado:any='na';
  public inicio                 : any;
  public texto                 : any;
  public fin                 : any;
  private _ID                  : String;

  public pais                 : any;
  public vpais  :any=[];

  public tipo                : any;
  public vtipo  :any=[];



  public sector                : any;
  public vsector  :any=[];


  public area                : any;
  public varea  :any=[];

  tiposus: any=[];

  f1: any='./assets/img/blanco.png';

  public foto                 : any;
  opimg: any;
  public ubicat                 : any;
  public fechat                 : any;

  public image              	: any;
  ubicacion:any;
  autor:any;
  presenta:any;
  text = 'hello world'
  item: FormControl;
  idempresa:any;
  estado:any;
  public pageTitle           	: string;
  createdCode = null;
  scannedCode = null;
  public vtarifa  :any=[];
  titulox:any;
  isDisablednit :any;
  constructor(  private _MODAL  		: ModalController,public alertCtrl: AlertController,private _IMAGE       : ImageProvider,public storage: Storage,  public msj: NotificationProvider, public translateService: TranslateService,public viewCtrl: ViewController,public authService:  Auth,public navCtrl 		: NavController,
    public navParams 	: NavParams,   private _FB          : FormBuilder,         public actionSheetCtrl: ActionSheetController,
          public events: Events)
  {
    this.translateService.get('APPX').subscribe((value) => {
      this.idempresa=value

      this.authService.getReg('todos/pais-plantilla/' + this.navParams.data.idempresa,'frmcats').then((data) => {
        this.vpais=data;
       });

       this.authService.getReg('todos/tipo-plantilla/' + this.navParams.data.idempresa,'frmcats').then((data) => {
        this.vtipo=data;
       });

       this.authService.getReg('todos/sector-plantilla/' + this.navParams.data.idempresa,'frmcats').then((data) => {
        this.vsector=data;
       });

       this.authService.getReg('todos/area-plantilla/' + this.navParams.data.idempresa,'frmcats').then((data) => {
        this.varea=data;
       });

  })

     this.form = this._FB.group({
      fecha     : ['', Validators.required],
      estado     : ['', Validators.required],
        nombre     : ['', Validators.required],
        titulo     : ['', Validators.required],
        texto     : ['', Validators.required],
        pais     : ['', Validators.required],
        tipo     : ['', Validators.required],
        sector     : ['', Validators.required],
        area     : ['', Validators.required],
   f1  : ['', Validators.required],
   f2: [''],
   ubicacion  : ['', Validators.required],
   autor  : ['', Validators.required],
   presenta  : ['', Validators.required]
     });





  }


  ionViewDidLoad() : void
  {
 //   this.portComponent.open();

  this.storage.get('tiposus').then((value) => {
    this.tiposus = value.split(',');




});


if(this.navParams.get("record")==null)
{




}
else {

   this._ID 				=	this.navParams.data.record._id;
   this.titulo				=	this.navParams.data.record.titulo;
   this.estado				=	this.navParams.data.record.estado;

   this.fecha				=	this.navParams.data.record.fecha;
   this.nombre		=	this.navParams.data.record.nombre;
   this.foto		=	this.navParams.data.record.f1;
   this.f1	=	this.navParams.data.record.f1;
   this.pais	=	this.navParams.data.record.pais;
   this.tipo	=	this.navParams.data.record.tipo;
   this.sector	=	this.navParams.data.record.sector;
   this.area	=	this.navParams.data.record.area;
   this.image	=	this.navParams.data.record.f1;
   this.ubicacion	=	this.navParams.data.record.ubicacion;
   this.autor	=	this.navParams.data.record.autor;
   this.presenta	=	this.navParams.data.record.presenta;

   this.form.controls['texto'].setValue(this.navParams.data.record.texto);
 //  this.texto			=	this.navParams.data.record.texto;


}



  }


  ngOnInit() {
    this.storage.get('username').then((value) => {
      this.username = value;
  });



  this.portNameControl = this._FB.control(null, Validators.required);

  this.portForm = this._FB.group({
    portName: this.portNameControl

  });

  this.portNameControl2 = this._FB.control(null, Validators.required);

  this.portForm2 = this._FB.group({
    portName: this.portNameControl2

  });

  this.portNameControl3 = this._FB.control(null, Validators.required);

  this.portForm3 = this._FB.group({
    portName: this.portNameControl3

  });


  this.portNameControl4 = this._FB.control(null, Validators.required);

  this.portForm4 = this._FB.group({
    portName: this.portNameControl4

  });


  }

  manageGallery() : void
  {
    let creat='Actualiza';
    if(this.navParams.get("record")==null)
    {  creat='Crea';
    }


     let   options       : any	     = {
    idempresa:this.navParams.data.idempresa,
    idcategoria:this.navParams.data.idcategoria,
     nombre : this.form.controls['nombre'].value,
    titulo : this.form.controls['titulo'].value,
     texto :this.form.get('texto').value.trim()     ,
     fecha : this.form.controls['fecha'].value,
     pais : this.form.controls['pais'].value.id,
     tipo : this.form.controls['tipo'].value.id,
     sector : this.form.controls['sector'].value.id,
     area : this.form.controls['area'].value.id,

   //  tipo:this.navParams.data.tipo,
     estado:this.form.controls['estado'].value,
     f1 : this.f1,
     f2:'',
     ubicacion:this.form.controls['ubicacion'].value,
     autor:this.form.controls['autor'].value,
     presenta:this.form.controls['presenta'].value,
     bitacora:{idempresa:this.navParams.data.idempresa,idafiliado:'',email: this.username,permiso:creat,accion:creat+' reglamento '}
    };


     if(this.navParams.get("record")==null)
     {

      if(options){
        this.authService.createReg('',options,'reglamentos').then((result) => {
          if (!this.form.valid) { return; }
          this.viewCtrl.dismiss(result);


      }, (err) => {
        this.msj.showError3('Error',err.error);
      });
      }


     }
     else
     {
      if(options){
        this.authService.createReg(this._ID,options,'reglamentos').then((result) => {
           if (!this.form.valid) { return; }
           this.viewCtrl.dismiss(result);
       }, (err) => {
        this.msj.showError3('Error',err.error);
       });
    }}

  }




  cancel() {
    this.viewCtrl.dismiss();
  }


  updateProfileImage(op) {
    this.opimg=op;
    let t1,t2;
    this.translateService.get('SELECCIONE_IMAGEN').subscribe((value) => {
      t1=value;
    });
    this.translateService.get('TOME_IMAGEN').subscribe((value) => {
      t2=value;
    });
    let actionSheet = this.actionSheetCtrl.create({
      title: t1,
      buttons: [//1930
        {
          text: t2,
          handler: () => {
            this.takePhotograph(op);
          }
        },
        {
          text: t1,
          handler: () => {
            this.selectImage(op);
          }
        }
      ]
    });
    actionSheet.present();
  }

  takePhotograph(op) : void
  {
   if (Camera['installed']()) {
     this._IMAGE
     .takePhotograph()
     .then((image)=>
     {
      this.f1 	= image.toString();
      // if(op==1){     this.f1 	= image.toString();}
     //  if(op==2) {    this.f2 	= image.toString();}

     })
     .catch((err)=>
     {
        console.log(err);
     });
   }
   else{
     this.fileInput.nativeElement.click();
   }
  }


  selectImage(op) : void
  {
   if (Camera['installed']()) {
     this._IMAGE
     .selectPhotograph()
     .then((image)=>
     {
        this.f1 	= image.toString();


     })
     .catch((err)=>
     {
        console.log(err);
     });
   }
   else{
     this.fileInput.nativeElement.click();
   }
  }

  public processWebImage(event,op): void {
    if(event.target.files && event.target.files[0]){
          const reader: FileReader = new FileReader();
          reader.onload = (readerEvent: any) => {

           this.f1 = readerEvent.target.result;
            this._IMAGE.generateFromImage(this.f1, 200, 200, 1, data => {
           this.f1 = data;
          });





   };
      reader.readAsDataURL(event.target.files[0]);
    }
}




//********************************************************************************************* */
port: Form;
portForm: FormGroup;
portNameControl: FormControl;


portForm2: FormGroup;
portNameControl2: FormControl;

portForm3: FormGroup;
portNameControl3: FormControl;

portForm4: FormGroup;
portNameControl4: FormControl;



onAddForm(event: {  component: IonicSelectableComponent  },catalogo:string,op:number) {

  switch (op) {
    case 1:
        this.portNameControl.reset();
        this.portNameControl.setValue(event.component.searchText);
      break;
    case 2:
        this.portNameControl2.reset();
        this.portNameControl2.setValue(event.component.searchText);
      break;
      case 3:
          this.portNameControl3.reset();
          this.portNameControl3.setValue(event.component.searchText);
        break;
        case 4:
            this.portNameControl4.reset();
            this.portNameControl4.setValue(event.component.searchText);
          break;

    }



  let addModal = this._MODAL .create('CatalogoSelectablePage',{ record : null,op:catalogo,frm:'frmcats'},{showBackdrop: true});
    addModal.onDidDismiss(data => {
      if (data) {

        switch (op) {
          case 1:
              this.portComponent.addItem(data);
              if(this.vpais.length==1)
              {
                this.portComponent.close().catch(() => { });
                this.portComponent.open().catch(() => { });
              }
            break;
          case 2:
              this.portComponent2.addItem(data);
              if(this.vtipo.length==1)
              {
                this.portComponent2.close().catch(() => { });
                this.portComponent2.open().catch(() => { });
              }
            break;
            case 3:
                this.portComponent3.addItem(data);
                if(this.vsector.length==1)
                {
                  this.portComponent3.close().catch(() => { });
                  this.portComponent3.open().catch(() => { });
                }
              break;
              case 4:
                  this.portComponent4.addItem(data);
                  if(this.varea.length==1)
                  {
                    this.portComponent4.close().catch(() => { });
                    this.portComponent4.open().catch(() => { });
                  }
                break;

          }



      }
    })
    addModal.present();

}



onSaveForm(event: {
  component: IonicSelectableComponent,
  item: Form

},catalogo:string) {

  let addModal = this._MODAL .create('CatalogoSelectablePage',{ record : {_id:event.item.id,nombre:event.item.nombre},op:catalogo,frm:'frmcats'},{showBackdrop: true});
  addModal.onDidDismiss(data => {
    if (data) {
      this.savePort(event.item,data.nombre);
      }
  });
  addModal.present();
}


savePort(port: Form,op) {    port.nombre = op;  }

deleteSelectedItem(op) {
  let itemToDeleteIndex;
var entra=0;
  this.vpais.forEach((selectedItem, itemIndex) => {
    if (selectedItem.id==op) {
      itemToDeleteIndex = itemIndex;
      entra=1;
    }
  });
  if(entra==1)    this.vpais.splice(itemToDeleteIndex, 1);
}

onDeleteForm(event: {
  component: IonicSelectableComponent,
  item: Form
},op:number) {
  let alert = this.alertCtrl.create({ title: 'Seguro de realizar esta operación?',
  buttons: [
    {text: 'Si',
      handler: () => {
        var aa:any;
        aa=event.item
        let recordID 		: string		= aa.id;
        this.authService.deleteReg(recordID+'/'+this.username+ '/' + this.idempresa + '/' + this.idafiliado,'frmcats').then((data) => {
          event.component.deleteItem(data);
          switch (op) {
            case 1:
                this.portComponent.clear();
              break;
            case 2:
                this.portComponent2.clear();
              break;
              case 3:
                  this.portComponent3.clear();
                break;
                case 4:
                    this.portComponent4.clear();
                  break;

            }



          this.deleteSelectedItem(recordID);
        }, (err) => {
          this.msj.showError3('Error',err.error);
        });

      }
    },
    {
      text: 'No',  handler: () => { }
    }
  ]
});
alert.present();
}




}



